      font-size: 14px;
      color: #2b2929;
      text-transform: capitalize;
      transition: all 0.4s ease; }
    nav.navigation > ul > li ul {
      position: absolute;
      top: 100%;
      left: 0;
      width: 250px;
      transition: all 0.4s ease;
      transition-delay: 0.2s;
      visibility: hidden;
      opacity: 0; }
      nav.navigation > ul > li ul li {
        position: relative;
        padding: 0; }
        nav.navigation > ul > li ul li a {
          display: block;
          position: relative;
          color: #a3a3a3;
          transition: all 0.4s ease; }
        nav.navigation > ul > li ul li.active > a, nav.navigation > ul > li ul li:hover > a {
          color: #f7edf1; }
        nav.navigation > ul > li ul li ul {
          top: 0;
          left: 100%; }
        nav.navigation > ul > li ul li:hover > ul {
          visibility: visible;
          opacity: 1; }
    nav.navigation > ul > li:hover > ul {
      visibility: visible;
      opacity: 1; }
    nav.navigation > ul > li:hover::before, nav.navigation > ul > li.active:before {
      width: 100%; }
    nav.navigation > ul > li::before {
      display: block;
      position: absolute;
      content: "";
      width: 0;
      height: 1px;
      left: 0;
      bottom: 0;
      background-color: #2b2929;
