    background-color: #f7edf1;
    cursor: pointer; }
    div.resp_menu div.resp_menu_toggle div {
      position: absolute;
      top: 50%;
      right: 15px;
      width: 20px;
      height: 1px;
      background-color: #fff;
      transition: all 0.2s ease; }
      div.resp_menu div.resp_menu_toggle div:first-child {
        margin-top: -8px; }
