.artform_wrap{
	display: inline-block;
	width: 20px;
	height: 20px;
	background-color: #f3f3f3;
	cursor: pointer;
	&.artradio{
		border-radius: 50%;
		&:before{
			display: block;
			position: absolute;
			content: "";
			width: 8px;
			height: 8px;
			left: 50%;
			top: 50%;
			transform: translate(-50%,-50%) scale(0);
			background-color: #fff;
			border-radius: 50%;
			opacity: 0;
			transition: all 0.4s ease;
		}
		&.checked{
			background-color: #a3a3a3;
			&:before{
				transform: translate(-50%,-50%) scale(1);
				opacity: 1;
			}
		}
		&.disabled{
			background-color: #fff;
			border: 1px solid #f3f3f3;
			input[type="radio"]{
				cursor: not-allowed;
			}
			&.checked{
				&:before{
					background-color: #f3f3f3;
					transform: translate(-50%,-50%) scale(1);
					opacity: 1;
				}
			}
		}
		input[type="radio"]{
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			opacity: 0;
			cursor: pointer;
		}
	}
	&.artchecker{
		border: 1px solid transparent;
		border-radius: 3px;
		&:before,
		&:after{
			position: absolute;
			content: "";
			width: 6px;
			height: 2px;
			left: 3px;
			top: 10px;
			transform: rotate(45deg);
			background-color: #fff;
			transition: all 0.4s ease;
			opacity: 0;
		}
		&:after{
			width: 10px;
			left: auto;
			top: 8px;
			right: 2px;
			transform: rotate(-45deg);
		}
		&.checked{
			background-color: #a3a3a3;
			&:before,
			&:after{
				opacity: 1;
			}
		}
		input[type="checkbox"]{
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			opacity: 0;
			cursor: pointer;
		}
		&.disabled{
			background-color: #fff;
			border: 1px solid #f3f3f3;
			input[type="checkbox"]{
				cursor: not-allowed;
			}
			&.checked{
				&:before,
				&:after{
					background-color: #f3f3f3;
					opacity: 1;
				}
			}
		}
	}
}
