span.select2{
	span.selection{
		span.select2-selection{
			height: 45px;
			background-color: #f3f3f3;
			border: none;
			border-radius: 5px;
			span.select2-selection__rendered{
				height: 100%;
				padding-left: 15px;
				padding-right: 30px;
				color: #777;
				line-height: 45px;
				vertical-align: middle;
				span.select2-selection__placeholder{
					color: #777;
				}
				span.select2-selection__clear{
					color: #777;
					font-size: 20px;
				}
			}
			span.select2-selection__arrow{
				width: 30px;
				height: 100%;
				top: 0;
				right: 0;
				&:before,
				&:after{
					display: block;
					position: absolute;
					content: "";
					width: 7px;
					height: 2px;
					left: 50%;
					top: 50%;
					background-color: #777;
					transform: translate(-50%,-50%) rotate(45deg);
				}
				&:before{
					margin-left: -4px;
				}
				&:after{
					transform: translate(-50%,-50%) rotate(-45deg);
				}
				b{
					display: none;
				}
			}
		}
	}
	&.select2-container--open{
		span.selection{
			span.select2-selection{
				span.select2-selection__arrow {
					&:before{
						transform: translate(-50%,-50%) rotate(-45deg);
					}
					&:after{
						transform: translate(-50%,-50%) rotate(45deg);
					}
				}
			}
		}
	}
}
span.select2-container{
	span.select2-dropdown{
		background-color: #f3f3f3;
		border: none;
		box-shadow: 0 5px 3px 0 rgba(#000, .1);
		span.select2-results{
			ul{
				li{
					&.select2-results__option--highlighted{
						background-color: #777;
					}
				}
			}
		}
	}
}
.file_uploader{
	input[type="file"] {
		position: absolute;
		left: 0;
		top: 0;
		width: 0;
		height: 0;
		opacity: 0;
	}
	label{
		height: 40px;
		color: #353535;
		background-color: #f3f3f3;
		border-radius: 3px;
		span{
			display: flex;
			align-items: center;
			position: absolute;
			left: 0;
			top: 0;
			right: 90px;
			bottom: 0;
			padding-left: 15px;
			color: #a3a3a3;
		}
		strong{
			display: flex;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;
			position: absolute;
			width: 90px;
			height: 100%;
			top: 0;
			right: 0;
			color: #fff;
			font-weight: normal;
			background-color: #777;
			border-radius: 0 3px 3px 0;
		}
